@font-face {
    font-family: 'SuisseIntl';
    src: url(/static/media/SuisseIntl-Book-WebM.0c6a404f.eot);
    src: url(/static/media/SuisseIntl-Book-WebM.904a0924.svg) format('svg');
    src: url(/static/media/SuisseIntl-Book-WebM.5f777e79.ttf) format('ttf');
    src: url(/static/media/SuisseIntl-Book-WebM.c20b7d11.woff) format('woff');
}
html {
    height: 100%;
}
body {
    min-height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'SuisseIntl';
    font-size: 15px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 2;
    letter-spacing: normal;
    background-color: #000000;
}
#root {
    height: 100vh;
}

.Menu {
    position: fixed;
    color: #ffffff;
    font-size: 15px;
    margin: 30px 30px 0 15px;
    min-width: 135px;
}
.Menu ul {
    margin-top: 10px;
    margin: 0;
    padding: 0;
    list-style: none;
}
.Menu li {
    height: 30px;
    line-height: 2;
    cursor: pointer;
}
.Menu li.active {
    opacity: 0.5;
}

.Menu_name {
    margin-bottom: 10px;
}
.Tile {
    position: relative;
    box-sizing: border-box;
    display: inline-block;
    margin: 0 25px 25px 0px;
    width: 180px;
    height: 180px;
    border: 1px solid #ffffff;
    padding: 15px;
    clear: both;
    overflow: hidden;
    vertical-align: top;
    cursor: pointer;
}

.Tile:hover {
    border-color: #fffaaa;
}

.Tile_name-noact {
    opacity: 0.5;
}

.Tile_name-nda {
    text-decoration: line-through;
}

.Tile_tags {
    width: 120px;
    position: absolute;
    bottom: 10px;
}
.Works {
    display: inline-block;
    position: relative;
}
.Works .Head {
    margin-bottom: 10px;
}

@media all and (max-width: 1440px)
{

}
.Bio .Content {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin-right: 30px;
    width: 420px;
}

.Bio .Content p {
    margin: 0;
}

.Bio .Header {
    margin-bottom: 10px;
}

.Bio .Text {
    margin-bottom: 2em;
}

.ContactsForm {
    width: 420px;
}

.ContactsForm .Input {
    font-size: 15px;
    width: 420px;
    font-family: 'SuisseIntl';
    padding: 15px;
    color: #ffffff;
    background-color: #000000;
    border: 1px solid #ffffff;
    margin-bottom: 15px;
    box-sizing: border-box;
}

.ContactsForm .Message {
    resize: none;
    height: 200px;
}

.ContactsForm .SendButton {
    font-size: 15px;
    width: 420px;
    font-family: 'SuisseIntl';
    height: 60px;
    color: #000000;
    background-color: #ffffff;
    border: none;
    font-weight: bold;
    margin-bottom: 15px;
}

.ContactsForm .Input::-webkit-input-placeholder {
    color: #ffffff;
}

.ContactsForm .Input::-ms-input-placeholder {
    color: #ffffff;
}

.ContactsForm .Input::placeholder {
    color: #ffffff;
}
.Contacts .Content {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin-right: 30px;
}

.Contacts .MessageForm_Header {
    margin-bottom: 10px;
}

.Contacts .MessageForm {
    width: 420px;
}

.Contacts .MessageForm .PersonalData {
    opacity: 0.5;
    width: 100%;
}

.Work {
    position: relative;
    margin-right: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
}

.Work_header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 10px;
    -webkit-flex: 0 0 30px;
            flex: 0 0 30px;
}

.Work_content {
    display: -webkit-flex;
    display: flex;
    border: 1px solid white;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: none;
    padding: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    z-index: 10;
}

.Work_content > span {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.Work_content__image {
    max-width: 100%;
    max-height: 100%;
}

.Cursor {
    position: absolute;
    cursor: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Work_image {
    position: relative;
    margin: auto;
    top: 90px;
}

.carousel-appear {
  opacity: 0.01;
}

.carousel-appear.carousel-appear-active {
  opacity: 1;
  transition: opacity .5s ease-in;
}

.carousel-enter {
  opacity: 0.01;
}

.carousel-enter.carousel-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.carousel-leave {
  opacity: 1;
}

.carousel-leave.carousel-leave-active {
  opacity: 0.01;
  transition: opacity 50ms ease-in;
}
.App {
  background-color: #000000;
  height: 100%;
  display: -webkit-flex;
  display: flex;
}

.App .ActiveArea {
    position: relative;
    color: #ffffff;
    margin-top: 30px;
    margin-left: 180px;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    padding-bottom: 20px;
}

.App a {
    color: #ffffff;
    text-decoration: underline;
    cursor: pointer;
}

.App a:visited {
    color: #ffffff;
}

